/**
 * 词卡魔法师 Agent (Word Card Wizard)
 * 
 * 功能：
 * 1. 理解用户输入（文字或语音转文字）
 * 2. 提取实体和属性
 * 3. 生成图片
 * 4. 返回词卡数据
 */

import { createAgent } from './agent-core';
import type { 
  ToolDefinition, 
  ToolContext, 
  ExtractedEntity, 
  WordCard,
  WordCardResult 
} from './types';

// 实体提取结果类型
interface EntityExtractionResult {
  entities: ExtractedEntity[];
}

// 图片生成结果类型
interface ImageGenerationResult {
  imageBase64: string;
  prompt: string;
}

/**
 * 工具 1: 实体提取
 * 使用 LLM 分析用户输入，提取实体信息
 */
const extractEntitiesTools: ToolDefinition<{ text: string }, EntityExtractionResult> = {
  name: 'extract_entities',
  description: '从用户输入中提取实体（物体、动物、交通工具等）及其属性（颜色、大小等）',
  parameters: {
    type: 'object',
    properties: {
      text: {
        type: 'string',
        description: '用户的输入文本',
      },
    },
    required: ['text'],
  },
  execute: async (input, context) => {
    const { ai } = context;
    
    const prompt = `分析以下文本，提取其中的实体（名词：动物、物体、交通工具、食物等）及其属性。

输入文本: "${input.text}"

请以 JSON 格式输出，格式如下：
{
  "entities": [
    {
      "name": "实体的中文名称",
      "nameEn": "English name",
      "category": "类别(animal/vehicle/food/object/person/place)",
      "attributes": ["属性1", "属性2"],
      "imagePrompt": "用于生成图片的英文描述，要具体生动"
    }
  ]
}

注意：
1. imagePrompt 要用英文，描述要具体，包含属性（如颜色、大小）
2. 如果有多个实体，都要提取出来
3. 只输出 JSON，不要其他文字`;

    try {
      const response = await ai.run('@cf/meta/llama-3.1-8b-instruct', {
        messages: [
          { role: 'system', content: '你是一个专业的语言分析助手，专门提取文本中的实体信息。只输出 JSON 格式。' },
          { role: 'user', content: prompt },
        ],
      }) as { response?: string };

      const text = response?.response || '';
      
      // 尝试解析 JSON
      const jsonMatch = text.match(/\{[\s\S]*\}/);
      if (jsonMatch) {
        const parsed = JSON.parse(jsonMatch[0]);
        return { entities: parsed.entities || [] };
      }
      
      return { entities: [] };
    } catch (error) {
      console.error('Entity extraction failed:', error);
      // 返回一个默认实体
      return {
        entities: [{
          name: input.text,
          nameEn: input.text,
          category: 'object',
          attributes: [],
          imagePrompt: input.text,
        }],
      };
    }
  },
};

/**
 * 工具 2: 图片生成
 * 使用 Cloudflare 的 Flux 模型生成图片
 */
const generateImageTool: ToolDefinition<{ prompt: string; entityName: string }, ImageGenerationResult> = {
  name: 'generate_image',
  description: '根据描述生成图片',
  parameters: {
    type: 'object',
    properties: {
      prompt: {
        type: 'string',
        description: '图片描述（英文）',
      },
      entityName: {
        type: 'string',
        description: '实体名称（用于标识）',
      },
    },
    required: ['prompt', 'entityName'],
  },
  execute: async (input, context) => {
    const { ai } = context;
    
    // 增强 prompt，使其更适合儿童风格
    const enhancedPrompt = `${input.prompt}, cute cartoon style, vibrant colors, child-friendly, simple background, high quality illustration`;
    
    try {
      const response = await ai.run('@cf/black-forest-labs/flux-1-schnell', {
        prompt: enhancedPrompt,
        num_steps: 4,  // Flux schnell 推荐 4 步
      }) as ArrayBuffer | ReadableStream;

      // 将图片转换为 base64
      let imageData: Uint8Array;
      
      if (response instanceof ReadableStream) {
        const reader = response.getReader();
        const chunks: Uint8Array[] = [];
        
        while (true) {
          const { done, value } = await reader.read();
          if (done) break;
          if (value) chunks.push(value);
        }
        
        const totalLength = chunks.reduce((acc, chunk) => acc + chunk.length, 0);
        imageData = new Uint8Array(totalLength);
        let offset = 0;
        for (const chunk of chunks) {
          imageData.set(chunk, offset);
          offset += chunk.length;
        }
      } else {
        imageData = new Uint8Array(response);
      }

      // 转换为 base64
      const base64 = btoa(String.fromCharCode(...imageData));
      
      return {
        imageBase64: `data:image/png;base64,${base64}`,
        prompt: enhancedPrompt,
      };
    } catch (error) {
      console.error('Image generation failed:', error);
      throw new Error(`图片生成失败: ${error instanceof Error ? error.message : String(error)}`);
    }
  },
};

/**
 * 工具 3: 语音转文字
 * 使用 Whisper 模型
 */
const speechToTextTool: ToolDefinition<{ audioBase64: string }, { text: string }> = {
  name: 'speech_to_text',
  description: '将语音转换为文字',
  parameters: {
    type: 'object',
    properties: {
      audioBase64: {
        type: 'string',
        description: 'Base64 编码的音频数据',
      },
    },
    required: ['audioBase64'],
  },
  execute: async (input, context) => {
    const { ai } = context;
    
    try {
      // 解码 base64 音频
      const audioData = Uint8Array.from(atob(input.audioBase64), c => c.charCodeAt(0));
      
      const response = await ai.run('@cf/openai/whisper', {
        audio: [...audioData],
      }) as { text?: string };

      return { text: response?.text || '' };
    } catch (error) {
      console.error('Speech to text failed:', error);
      throw new Error(`语音识别失败: ${error instanceof Error ? error.message : String(error)}`);
    }
  },
};

/**
 * 词卡魔法师 Agent 定义
 */
export const wordCardAgent = createAgent({
  name: 'word_card_wizard',
  description: '词卡魔法师 - 帮助小朋友学习词汇',
  model: '@cf/meta/llama-3.1-8b-instruct',
  
  systemPrompt: `你是"词卡魔法师"，一个帮助小朋友学习词汇的 AI 助手。

你的任务流程：
1. 理解用户说的词或句子
2. 使用 extract_entities 工具提取其中的实体（物体、动物等）
3. 对每个实体，使用 generate_image 工具生成可爱的卡通图片
4. 返回完整的词卡信息

请始终保持友好、活泼的语气，因为你的用户是小朋友！

输出格式要求：
- 提取实体时，确保包含中英文名称
- 图片描述要适合生成儿童风格的卡通图片
- 最终结果要包含所有词卡信息`,

  tools: [
    extractEntitiesTools,
    generateImageTool,
    speechToTextTool,
  ],
});

/**
 * 执行词卡生成的主函数
 * 这是一个更直接的执行流程，不依赖 LLM 的工具调用解析
 */
export async function generateWordCards(
  input: string,
  ai: any,
  onStep?: (step: { type: string; content: string }) => void
): Promise<WordCardResult> {
  const steps: Array<{ type: string; content: string }> = [];
  
  const addStep = (type: string, content: string) => {
    steps.push({ type, content });
    onStep?.({ type, content });
  };

  addStep('user_input', input);
  addStep('understanding', '正在理解你说的话...');

  // Step 1: 提取实体
  addStep('tool_call', '调用实体提取工具');
  
  const context: ToolContext = {
    ai,
    sessionId: Math.random().toString(36).substring(2),
    addStep: () => {},
  };

  const extractionResult = await extractEntitiesTools.execute({ text: input }, context);
  const entities = extractionResult.entities;
  
  addStep('tool_result', `发现了 ${entities.length} 个实体: ${entities.map(e => e.name).join(', ')}`);

  // Step 2: 为每个实体生成图片
  const cards: WordCard[] = [];
  
  for (const entity of entities) {
    addStep('tool_call', `正在为"${entity.name}"画画...`);
    
    try {
      const imageResult = await generateImageTool.execute(
        { prompt: entity.imagePrompt, entityName: entity.name },
        context
      );
      
      cards.push({
        entity,
        imageUrl: imageResult.imageBase64,
        pronunciation: `/${entity.nameEn.toLowerCase()}/`, // 简化的音标
      });
      
      addStep('tool_result', `"${entity.name}"的图片画好了！`);
    } catch (error) {
      addStep('error', `生成"${entity.name}"的图片时出错了`);
      // 继续处理下一个实体
    }
  }

  addStep('completed', '词卡生成完成！');

  return {
    originalInput: input,
    entities,
    cards,
  };
}
