/**
 * Agent API 端点
 * 处理词卡魔法师的请求
 */

import { generateWordCards } from './lib/word-card-agent';
import type { Env, AgentRequest, AgentResponse, AgentRunState, AgentStep } from './lib/types';

// CORS 头
const corsHeaders = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
  'Access-Control-Allow-Headers': 'Content-Type',
};

// 处理 OPTIONS 请求
export const onRequestOptions: PagesFunction<Env> = async () => {
  return new Response(null, {
    status: 204,
    headers: corsHeaders,
  });
};

// 处理 POST 请求
export const onRequestPost: PagesFunction<Env> = async (context) => {
  const { request, env } = context;

  try {
    const body = await request.json() as AgentRequest;
    const { input, inputType, audioData, sessionId } = body;

    // 初始化运行状态
    const runState: AgentRunState = {
      sessionId: sessionId || Math.random().toString(36).substring(2),
      phase: 'idle',
      steps: [],
      currentStepIndex: -1,
      startTime: Date.now(),
    };

    let textInput = input;

    // 如果是语音输入，先转换为文字
    if (inputType === 'audio' && audioData) {
      runState.phase = 'understanding';
      runState.steps.push({
        id: Math.random().toString(36).substring(2),
        type: 'tool_call',
        content: '正在识别语音...',
        timestamp: Date.now(),
        metadata: { phase: 'understanding' },
      });

      try {
        // 解码 base64 音频
        const binaryString = atob(audioData);
        const bytes = new Uint8Array(binaryString.length);
        for (let i = 0; i < binaryString.length; i++) {
          bytes[i] = binaryString.charCodeAt(i);
        }

        const whisperResponse = await env.AI.run('@cf/openai/whisper', {
          audio: [...bytes],
        }) as { text?: string };

        textInput = whisperResponse?.text || input;

        runState.steps.push({
          id: Math.random().toString(36).substring(2),
          type: 'tool_result',
          content: `语音识别结果: "${textInput}"`,
          timestamp: Date.now(),
        });
      } catch (error) {
        console.error('Speech recognition failed:', error);
        // 如果语音识别失败，使用原始输入
        runState.steps.push({
          id: Math.random().toString(36).substring(2),
          type: 'tool_result',
          content: `语音识别失败，使用文字输入`,
          timestamp: Date.now(),
        });
      }
    }

    // 执行词卡生成
    const result = await generateWordCards(
      textInput,
      env.AI,
      (step) => {
        runState.steps.push({
          id: Math.random().toString(36).substring(2),
          type: step.type as AgentStep['type'],
          content: step.content,
          timestamp: Date.now(),
          metadata: { phase: runState.phase },
        });
      }
    );

    // 完成
    runState.phase = 'completed';
    runState.endTime = Date.now();

    const response: AgentResponse = {
      success: true,
      result,
      runState,
    };

    return new Response(JSON.stringify(response), {
      status: 200,
      headers: {
        'Content-Type': 'application/json',
        ...corsHeaders,
      },
    });

  } catch (error) {
    console.error('Agent API error:', error);

    const response: AgentResponse = {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
      runState: {
        sessionId: 'error',
        phase: 'error',
        steps: [{
          id: 'error',
          type: 'final_output',
          content: `错误: ${error instanceof Error ? error.message : 'Unknown error'}`,
          timestamp: Date.now(),
          metadata: { phase: 'error' },
        }],
        currentStepIndex: 0,
        startTime: Date.now(),
        endTime: Date.now(),
      },
    };

    return new Response(JSON.stringify(response), {
      status: 500,
      headers: {
        'Content-Type': 'application/json',
        ...corsHeaders,
      },
    });
  }
};

// 健康检查
export const onRequestGet: PagesFunction<Env> = async () => {
  return new Response(JSON.stringify({ status: 'ok', agent: 'word_card_wizard' }), {
    status: 200,
    headers: {
      'Content-Type': 'application/json',
      ...corsHeaders,
    },
  });
};
