/**
 * Agent 核心运行时
 * 借鉴 Pydantic AI 的设计：Agent 定义 + 工具 + 运行时
 */

import type {
  AgentDefinition,
  AgentRunState,
  AgentStep,
  AgentPhase,
  ToolDefinition,
  ToolContext,
  ToolCall,
  Ai,
} from './types';

// 生成唯一 ID
function generateId(): string {
  return Math.random().toString(36).substring(2, 15);
}

/**
 * Agent 运行时类
 * 负责协调 LLM 调用、工具执行、状态管理
 */
export class AgentRuntime {
  private agent: AgentDefinition;
  private ai: Ai;
  private runState: AgentRunState;

  constructor(agent: AgentDefinition, ai: Ai, sessionId?: string) {
    this.agent = agent;
    this.ai = ai;
    this.runState = {
      sessionId: sessionId || generateId(),
      phase: 'idle',
      steps: [],
      currentStepIndex: -1,
      startTime: Date.now(),
    };
  }

  /**
   * 添加执行步骤
   */
  private addStep(step: Omit<AgentStep, 'id' | 'timestamp'>): AgentStep {
    const fullStep: AgentStep = {
      ...step,
      id: generateId(),
      timestamp: Date.now(),
    };
    this.runState.steps.push(fullStep);
    this.runState.currentStepIndex = this.runState.steps.length - 1;
    return fullStep;
  }

  /**
   * 更新运行阶段
   */
  private setPhase(phase: AgentPhase): void {
    this.runState.phase = phase;
  }

  /**
   * 获取工具上下文
   */
  private getToolContext(): ToolContext {
    return {
      ai: this.ai,
      sessionId: this.runState.sessionId,
      addStep: (step) => this.addStep(step),
    };
  }

  /**
   * 构建工具描述（用于 LLM function calling）
   */
  private buildToolsDescription(): string {
    if (this.agent.tools.length === 0) return '';

    const toolsDesc = this.agent.tools.map(tool => {
      const paramsDesc = Object.entries(tool.parameters.properties)
        .map(([name, prop]) => `    - ${name} (${prop.type}): ${prop.description}`)
        .join('\n');
      return `- ${tool.name}: ${tool.description}\n  参数:\n${paramsDesc}`;
    }).join('\n\n');

    return `\n\n你可以使用以下工具：\n${toolsDesc}\n\n当需要使用工具时，请按以下 JSON 格式输出：\n{"tool": "工具名", "params": {参数对象}}`;
  }

  /**
   * 解析 LLM 输出中的工具调用
   */
  private parseToolCall(response: string): { tool: string; params: Record<string, unknown> } | null {
    // 尝试匹配 JSON 格式的工具调用
    const jsonMatch = response.match(/\{[\s\S]*?"tool"[\s\S]*?\}/);
    if (jsonMatch) {
      try {
        const parsed = JSON.parse(jsonMatch[0]);
        if (parsed.tool && typeof parsed.tool === 'string') {
          return {
            tool: parsed.tool,
            params: parsed.params || {},
          };
        }
      } catch {
        // JSON 解析失败，继续尝试其他方式
      }
    }
    return null;
  }

  /**
   * 执行单个工具
   */
  private async executeTool(
    toolName: string,
    params: Record<string, unknown>
  ): Promise<{ success: boolean; result?: unknown; error?: string }> {
    const tool = this.agent.tools.find(t => t.name === toolName);
    if (!tool) {
      return { success: false, error: `未知工具: ${toolName}` };
    }

    const toolCall: ToolCall = {
      id: generateId(),
      name: toolName,
      description: tool.description,
      input: params,
      status: 'running',
      startTime: Date.now(),
    };

    this.addStep({
      type: 'tool_call',
      content: `调用工具: ${toolName}`,
      metadata: { tool: toolCall, phase: 'tool_calling' },
    });

    try {
      const result = await tool.execute(params, this.getToolContext());
      toolCall.output = result;
      toolCall.status = 'completed';
      toolCall.endTime = Date.now();

      this.addStep({
        type: 'tool_result',
        content: `工具 ${toolName} 执行完成`,
        metadata: { tool: toolCall },
      });

      return { success: true, result };
    } catch (error) {
      toolCall.status = 'error';
      toolCall.error = error instanceof Error ? error.message : String(error);
      toolCall.endTime = Date.now();

      this.addStep({
        type: 'tool_result',
        content: `工具 ${toolName} 执行失败: ${toolCall.error}`,
        metadata: { tool: toolCall },
      });

      return { success: false, error: toolCall.error };
    }
  }

  /**
   * 调用 LLM
   */
  private async callLLM(messages: Array<{ role: string; content: string }>): Promise<string> {
    this.addStep({
      type: 'llm_thinking',
      content: '正在思考...',
      metadata: { model: this.agent.model, phase: 'understanding' },
    });

    try {
      const response = await this.ai.run(this.agent.model, { messages }) as { response?: string };
      const text = response?.response || '';
      
      this.addStep({
        type: 'llm_thinking',
        content: text.substring(0, 200) + (text.length > 200 ? '...' : ''),
        metadata: { model: this.agent.model },
      });

      return text;
    } catch (error) {
      const errorMsg = error instanceof Error ? error.message : String(error);
      throw new Error(`LLM 调用失败: ${errorMsg}`);
    }
  }

  /**
   * 主运行方法 - ReAct 模式
   * Think → Act → Observe → 循环
   */
  async run(userInput: string): Promise<{ result: unknown; runState: AgentRunState }> {
    this.setPhase('understanding');
    
    // 记录用户输入
    this.addStep({
      type: 'user_input',
      content: userInput,
      metadata: { phase: 'understanding' },
    });

    // 构建初始消息
    const systemPrompt = this.agent.systemPrompt + this.buildToolsDescription();
    const messages: Array<{ role: string; content: string }> = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: userInput },
    ];

    // ReAct 循环（最多 5 轮，防止无限循环）
    const maxIterations = 5;
    let result: unknown = null;
    const toolResults: Record<string, unknown> = {};

    for (let i = 0; i < maxIterations; i++) {
      this.setPhase(i === 0 ? 'planning' : 'tool_calling');
      
      // 调用 LLM
      const llmResponse = await this.callLLM(messages);
      
      // 检查是否需要调用工具
      const toolCallRequest = this.parseToolCall(llmResponse);
      
      if (toolCallRequest) {
        // 执行工具
        const toolExecution = await this.executeTool(
          toolCallRequest.tool,
          toolCallRequest.params
        );

        if (toolExecution.success) {
          toolResults[toolCallRequest.tool] = toolExecution.result;
          
          // 将工具结果添加到对话中
          messages.push({ role: 'assistant', content: llmResponse });
          messages.push({
            role: 'user',
            content: `工具 ${toolCallRequest.tool} 的执行结果：${JSON.stringify(toolExecution.result)}`,
          });
        } else {
          // 工具执行失败
          messages.push({ role: 'assistant', content: llmResponse });
          messages.push({
            role: 'user',
            content: `工具执行失败：${toolExecution.error}`,
          });
        }
      } else {
        // 没有工具调用，认为是最终回答
        result = {
          response: llmResponse,
          toolResults,
        };
        break;
      }
    }

    // 完成
    this.setPhase('completed');
    this.runState.endTime = Date.now();

    this.addStep({
      type: 'final_output',
      content: '任务完成',
      metadata: { phase: 'completed' },
    });

    return {
      result,
      runState: this.runState,
    };
  }

  /**
   * 获取当前运行状态
   */
  getRunState(): AgentRunState {
    return this.runState;
  }
}

/**
 * 创建 Agent 的工厂函数（类似 Pydantic AI 的 Agent 类）
 */
export function createAgent(definition: AgentDefinition) {
  return {
    definition,
    run: async (userInput: string, ai: Ai, sessionId?: string) => {
      const runtime = new AgentRuntime(definition, ai, sessionId);
      return runtime.run(userInput);
    },
  };
}
